<script>
    import MyForms from './my-forms/index.vue';
    import MyPay from './my-pay.vue';
    import MyLeave from './my-leave.vue';
    import MyDetails from './my-details.vue';
    import MyManager from './my-manager.vue';
    import MyActions from './my-actions.vue';

    export default {
        props: {
            version: String,
            configHost: String
        },
        data() {
            return {
                widgets: [],
                apps: {}
            }
        },
        methods: {
            isWidgetShown(name) {
                return this.widgets.indexOf(name) >= 0;
            },
            onDashboardReady(config) {
                this.widgets = [];
                this.apps = {};
                config.Apps.forEach((app) => {
                    if (app.Widgets) {
                        app.Widgets.forEach((w) => {
                            if (w.Enabled) {
                                if (Affinity.user.EmployeeNo >= 5000000 && w.Name != 'Inbox') {
                                    return;
                                }
                                this.widgets.push(w.Name);
                            }
                        });
                    }
                    this.apps[app.Name] = app;
                });
            }
        },
        components: {
            MyForms,
            MyPay,
            MyLeave,
            MyDetails,
            MyManager,
            MyActions
        }
    }
</script>

<template>
    <dashboard config-url="/Config/Dashboard" info-url="/Auth/GetInfo" logout-url="/Auth/Logout" @ready="onDashboardReady" :version="version">
        <div class="widgets">
            <my-actions :apps="apps"></my-actions><!--
            --><my-forms v-if="isWidgetShown('Inbox')"></my-forms><!--
            --><my-pay v-if="isWidgetShown('MyPay')"></my-pay><!--
            --><my-details v-if="isWidgetShown('MyDetails')"></my-details><!--
            --><my-manager v-if="isWidgetShown('MyManager')"></my-manager><!--
            --><my-leave v-if="isWidgetShown('Balance')"></my-leave>
        </div>
    </dashboard>
</template>

<style lang="scss">
    body.newui .dashboard {
        .widgets {
            @media (min-width: 1024px) {
                .my-actions-widget {
                    width: 40%;
                }
                .my-forms-widget {
                    width: 60%;
                }
                .my-details-widget,
                .my-manager-widget,
                .my-pay-widget,
                .my-leave-widget {
                    width: 50%;
                }
            }

            @media (min-width: 1440px) {
                .my-actions-widget {
                    width: 33.33%;
                }
                .my-forms-widget {
                    width: 66.66%;
                }
            }

            @media (min-width: 1600px) {
                .my-actions-widget {
                    width: 25%;
                }
                .my-forms-widget {
                    width: 75%;
                }
            }

            @media (min-width: 1920px) {
                .my-details-widget,
                .my-manager-widget,
                .my-pay-widget,
                .my-leave-widget {
                    width: 25%;
                }
            }
        }
    }
</style>
