<script>
    import { description, stateName, completedBy, stateEnteredAt, viewButtons } from './columns';

    let colStateEnteredAt = stateEnteredAt();
    colStateEnteredAt.header = 'Completed At';

    export default {
        props: {
            data: Array
        },
        data() {
            return {
                columns: [
                    description(),
                    stateName(),
                    completedBy(),
                    colStateEnteredAt,
                    viewButtons()
                ]
            }
        }
    }
</script>

<template>
    <div class="ui-inbox-box">
        <grid :columns="columns" :data="data"></grid>
    </div>
</template>
