<script>
    export default {
        props: {
            page: {
                type: Number,
                default: 0
            },
            pageSize: {
                type: Number,
                default: 50
            },
            maxPages: {
                type: Number,
                default: 15
            },
            itemLabel: {
                type: String,
                default: 'item(s)'
            },
            rowCount: {
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                start: 0,
                end: 0,
                prevEnabled: false,
                nextEnabled: false,
                buttons: []
            }
        },
        watch: {
            page() {
                this.refresh();
            },
            rowCount() {
                this.refresh();
            }
        },
        mounted() {
            this.refresh();
        },
        methods: {
            refresh() {
                let pageCount = this.pageSize > 0 ? Math.floor(this.rowCount / this.pageSize) : 0;
                if (this.rowCount % this.pageSize > 0) pageCount++;

                if (this.page < 0) this.page = 0;
                else if (this.page >= pageCount) this.page = pageCount - 1;

                this.prevEnabled = this.page !== 0;
                this.nextEnabled = this.page !== pageCount - 1;

                this.start = this.rowCount === 0 ? -1 : this.page * this.pageSize;
                this.end = this.start + (this.page === pageCount - 1 ? this.rowCount % this.pageSize : this.pageSize);
                if (this.end === this.start) this.end += this.pageSize;

                var pages = [];
                if (pageCount > this.maxPages) {
                    var sectionCount = Math.floor(this.maxPages / 3);
                    var wingLength = Math.floor(sectionCount / 2);

                    var length = this.maxPages - sectionCount;
                    var appended = this.page + wingLength < length;

                    for (var i = 0; i < (appended ? length : sectionCount) ; i++) {
                        pages.push(i);
                    }

                    appended = this.page - wingLength >= pageCount - length;

                    for (i = pageCount - 1; i >= (pageCount - (appended ? length : sectionCount)) ; i--) {
                        pages.push(i);
                    }

                    if (!appended) {
                        for (var i = this.page - wingLength; i <= this.page + wingLength; i++) {
                            if (i < 0 || i > pageCount - 1) pages.push(this.page - i);
                            else pages.push(i);
                        }
                    }
                }
                else {
                    for (var i = 0; i < pageCount ; i++) {
                        pages.push(i);
                    }
                }

                pages.sort(function (a, b) { return a - b; });

                var prevNum = -1;
                this.buttons = [];
                pages.unique().each((num) => {
                    if (prevNum != num - 1) {
                        this.buttons.push({
                            text: '...',
                            action: () => {}
                        })
                    }

                    this.buttons.push({
                        num,
                        text: num + 1,
                        action: this.gotoPage,
                        selected: num === this.page
                    })

                    prevNum = num;
                });
            },
            prevClick() {
                if (!this.prevEnabled) return;
                this.gotoPage(this.page - 1);
            },
            nextClick() {
                if (!this.nextEnabled) return;
                this.gotoPage(this.page + 1);
            },
            gotoPage(num) {
                this.$emit('goto', num);
            }
        }
    }
</script>

<template>
    <div class="pagination">
        <div class="nav-buttons">
            <span @click="prevClick" :class="{ 'disabled': !prevEnabled }">previous</span>
            <span>
                <span v-for="b in buttons" :class="{ selected: b.selected }" @click="b.action(b.num)">{{ b.text }}</span>
            </span>
            <span @click="nextClick" :class="{ 'disabled': !nextEnabled }">next</span>
        </div>
        <span class="desc">Showing {{ itemLabel }} {{ start + 1 }} to {{ end }} of {{ rowCount }}</span>
    </div>
</template>
