<script>
    export default {
        props: {
            apps: Object
        },
        data() {
            return {
                leaveStatus: 'Requesting data...',
                formsStatus: 'Requesting data...',
                timesheetStatus: 'Requesting data...'
            }
        },
        computed: {
            showLeave() {
                return this.showApp('Leave');
            },
            showTimesheets() {
                return this.showApp('Timesheets');
            },
            showForms() {
                return this.showApp('CleverForms');
            },
            showActionCentre() {
              return this.showLeave || this.showTimesheets || this.showForms;
            },
        },
        watch: {
            apps(apps) {
                if (apps) this.load();
            }
        },
        methods: {
            load() {
                if (this.showLeave) this.countLeave();
                if (this.showTimesheets) this.countTimesheets();
                if (this.showForms) this.countForms();
            },
            showApp(name) {
                let app = this.apps[name];
                return app && app.Link && app.Link.Enabled;
            },
            getAppTitle(appName) {
                let app = this.apps[appName];
                return app && app.Link && app.Link.Text;
            },
            countLeave() {
                this.$ajax.post('/Leave/CountPending')
                    .then(resp => {
                        if (resp.data.Data > 0) this.leaveStatus = `<span style="color: red;">You have leave to approve.</span>`;
                        else this.leaveStatus = 'Apply for leave.'
                    })
                    .catch(() => {
                        this.leaveStatus = 'Unable to load the information.';
                    });
            },
            countForms() {
                this.$ajax.post('/CleverForms/GetInboxData')
                    .then(resp => {
                        let pending = resp.data.ToAction || [];

                        if (pending.length > 0) {
                            this.formsStatus = '<span style="color: red;">You have forms to action.</span>';
                        }
                        else this.formsStatus = 'Start a new form.';
                    })
                    .catch(() => {
                        this.formsStatus = 'Unable to load the information.';
                    });
            },
            countTimesheets() {
                this.$ajax.post('/Timesheets/CountPending')
                    .then(resp => {
                        if (resp.data.error) this.timesheetStatus = 'Unable to load the information.';
                        if (resp.data.data > 0) this.timesheetStatus = '<span style="color: red;">You have timesheets to approve.</span>';
                        else this.timesheetStatus = 'Check for timesheets.';
                    })
                    .catch(() => {
                        this.timesheetStatus = 'Unable to load the information.';
                    });
            }
        },
        created() {
            this.load();
        }
    }
</script>
<template>
    <tile title="Action Centre" v-if="showActionCentre" icon="icon-new" help="icon-help-round" class="my-actions-widget">
        <div class="action-list">
            <a class="timesheets-app" href="/Timesheets" v-if="showTimesheets">
                <div class="col col-icon icon-clock theme"></div>
                <div class="col col-body">
                    <div class="title theme">{{ getAppTitle('Timesheets') }}</div>
                    <div class="status-text" v-html="timesheetStatus"></div>
                </div>
                <span class="col go-icon icon-arrow-line-big-right"></span>
            </a>
            <a class="leave-app" href="/Leave" v-if="showLeave">
                <div class="col col-icon icon-plane theme"></div>
                <div class="col col-body">
                    <div class="title theme">{{ getAppTitle('Leave') }}</div>
                    <div class="status-text" v-html="leaveStatus"></div>
                </div>
                <span class="col go-icon icon-arrow-line-big-right"></span>
            </a>
            <a class="forms-app" href="/CleverForms/Inbox" v-if="showForms">
                <div class="col col-icon icon-page theme"></div>
                <div class="col col-body">
                    <div class="title theme">{{ getAppTitle('CleverForms') }}</div>
                    <div class="status-text" v-html="formsStatus"></div>
                </div>
                <span class="col go-icon icon-arrow-line-big-right"></span>
            </a>
        </div>
    </tile>
</template>

<style lang="scss">
    .my-actions-widget {
        .title-box {
          width: 100%;
        }
        .title-box a {
          float: right;
          color: #fff;
        }
        .action-list {
            padding: 0;
            margin: 0;

            & > a {
                width: 100%;
                height: 60px;
                display: table;
                cursor: pointer;
                border-bottom: 1px solid #ccc;

                & + li {
                    margin-top: 5px;
                }
                .col {
                    display: table-cell;
                    vertical-align: middle;
                    box-sizing: border-box;
                }
                .col-icon {
                    font-size: 30px;
                    width: 60px;
                    padding-left: 10px;
                    border-left: 3px solid #000;
                }
                .col-body {
                    color: #666;

                    .title {
                        font-size: 16px;
                    }
                    .status-text {
                        margin-top: 5px;
                        font-style: italic;
                    }
                }
                .go-icon {
                    font-size: 30px;
                    width: 40px;
                    padding-right: 10px;
                    color: #444;
                    text-align: right;
                }
                &:hover {
                    background-color: #ddd;
                }
                &.leave-app {
                    .theme {
                        color: #4CAF50;
                        border-color: #4CAF50;
                    }
                }
                &.timesheets-app {
                    .theme {
                        color: #FF9800;
                        border-color: #FF9800;
                    }
                }
                &.forms-app {
                    .theme {
                        color: #2196F3;
                        border-color: #2196F3;
                    }
                }
            }
        }
    }
</style>
