<script>
    import { description, stateName, stateEnteredAt, editButtons } from './columns';

    export default {
        props: {
            data: Array
        },
        data() {
            return {
                columns: [
                    description(),
                    stateName(),
                    stateEnteredAt(),
                    editButtons()
                ]
            }
        },
        methods:{
            refreshGrid() {
                this.$emit("refreshgrid");
            }
        }
    }
</script>

<template>
    <div class="ui-inbox-box">
        <grid v-on:refreshgrid="refreshGrid" ref="grid"  :columns="columns" :data="data"></grid>
    </div>
</template>
