import moment from "moment";
import Vue from "vue";

export function description() {
  return {
    name: "DisplayName",
    header: "Name",
    sortable: true,
    type: "html",
    indicate() {
      return "none";
    },
    format(value, data) {
      if (data.ShareOrDelegate) {
        if (data.InstanceKey) value = `(${data.InstanceKey}) ${value}`;
        value += `<span class="tag ${data.ShareOrDelegate.toLowerCase()}">${
          data.SharedBy
        }</span>`;
      }

      return value;
    }
  };
}

export function stateName() {
  return {
    name: "StateName",
    header: "Status",
    sortable: true
  };
}

export function stateAssigneeName() {
  return {
    name: "StateAssigneeName",
    header: "Assigned To",
    sortable: true
  };
}

export function stateEnteredAt() {
  return {
    name: "StateEnteredAt",
    header: "Completed At",
    type: "date",
    sortable: true,
    format(value) {
      return moment(value).format("DD/MM/YYYY h:mm a");
    }
  };
}

export function completedBy() {
  return {
    name: "CompletedBy",
    header: "Completed By",
    sortable: true
  };
}

export function viewButtons() {
  return {
    header: "",
    buttons: [
      {
        color: "blue",
        icon: "icon-page",
        text: "View",
        href(row) {
          return "/CleverForms/ViewInstance/" + row.InstanceId;
        }
      }
    ]
  };
}

export function editButtons() {
  return {
    header: "",
    buttons: [
      {
        icon: "icon-pencil",
        text: "Edit",
        color: "blue",
        href(row) {
          return "/CleverForms/EditInstance/" + row.InstanceId;
        }
      },
      {
        icon: "icon-cross-round",
        text: "",
        color: "red",
        showModal: true,
        modalAcceptBtnText: "Confirm",
        modalCancelBtnText: "Cancel",
        modalText: "Are you sure you want to delete this form?",
        modalAcceptBtnFn(target) {
          target.loading = true;
          Vue.ajax
            .get("/CleverForms/DeleteInbox/" + target.modalData.InstanceId)
            .then(resp => {
              if (resp.data.success) {
                target.$emit("refreshgrid");
                target.showModal = false;
                target.loading = false;
              }
            })
            .catch();
        },
        modalCancelBtnFn(target) {
          target.showModal = false;
        }
      }
    ]
  };
}
