<script>
import CfWidget from "./cf-widget/index.vue";

export default CfWidget.extend({
  data() {
    return {
      department: "",
      employeeNo: "",
      companyNo: "",
      position: "",
      commonName: "",
      user: Affinity.user,
      isLoading: true,
      localData: [],
      loadError: null
    };
  },
  computed: {
    fullName() {
      return `${this.user.FirstName} ${this.user.LastName}`;
    },
    items() {
      return this.localData.filter(item => {
        return (
          item.Type === "Separator" ||
          item.Type === "Subheading" ||
          item.Type === "Explanation" ||
          item.Type === "Paragraph" ||
          item.Type === "YoutubeInput" ||
          item.Value
        );
      });
    }
  },
  created() {
    this.$ajax
      .post("/CleverForms/GetUserDetails")
      .then(resp => {
        if (resp.data.error) {
          this.loadError = "Could not load personal details.";
          return;
        }

        let toRemove = [];

        resp.data.forEach(f => {
          switch (f.Key) {
            case "Department":
              this.department = f;
              toRemove.push(f);
              break;
            case "EmployeeNo":
              this.employeeNo = f;
              toRemove.push(f);
              break;
            case "CompanyNo":
              this.companyNo = f;
              toRemove.push(f);
              break;
            case "CommonName":
              this.commonName = f;
              toRemove.push(f);
              break;
          }

          if (f.InputType == "DateTime" && f.Value) {
            f.Value = new Date(f.Value).format("%a %e %b %Y");
          }
        });

        toRemove.forEach(f => {
          let index = resp.data.indexOf(f);
          resp.data.splice(index, 1);
        });

        this.localData = resp.data;
      })
      .catch(() => {
        this.loadError = "Could not load personal details.";
      })
      .then(() => {
        this.isLoading = false;
      });
  }
});
</script>

<template>
  <tile
    title="My Details"
    icon="icon-user"
    class="cf-widget my-details-widget"
    :loading="isLoading"
  >
    <div v-if="loadError">{{ loadError }}</div>
    <div v-else>
      <div class="field-display item" v-if="commonName">
        <div class="label">{{ commonName.Label }}</div>
        <div class="value">{{ commonName.Value }}</div>
      </div>
      <div class="field-display item" v-if="employeeNo">
        <div class="label">{{ employeeNo.Label }}</div>
        <div class="value">{{ employeeNo.Value }}</div>
      </div>
      <div class="field-display item" v-if="companyNo">
        <div class="label">{{ companyNo.Label }}</div>
        <div class="value">{{ companyNo.Value }}</div>
      </div>
      <component
        v-for="(item, index) in items"
        :is="item.Type"
        :data="item"
        class="item"
        :key="index"
      ></component>
    </div>
  </tile>
</template>

<style lang="scss">
.my-details-widget {
  table {
    width: 100%;

    td + td {
      text-align: right;
    }
  }
}
</style>
