<script>
import CfWidget from "./cf-widget/index.vue";

export default {
  data() {
    return {
      data: null,
      loading: true,
      error: null
    };
  },
  created() {
    this.$ajax
      .post("/CleverForms/GetPayDetails")
      .then(resp => {
        if (resp.data.error) {
          return console.log(resp.data.error);
        }

        resp.data.forEach(f => {
          if (f.Key === "TaxCode") {
            f.Value = `[${f.KeyValue}] ${f.Value}`;
          } else if (
            f.Value &&
            (f.Key === "AnnualSalary" || f.InputType === "Currency")
          ) {
            f.Value = "$" + f.Value;
          }
        });

        this.data = resp.data;
      })
      .catch(() => {
        this.error = "Could not load pay details.";
      })
      .then(() => {
        this.loading = false;
      });
  },
  components: {
    CfWidget
  }
};
</script>

<template>
  <cf-widget
    title="My Pay"
    icon="icon-credit-card"
    class="my-pay-widget"
    :data="data"
    :loading="loading"
    :error="error"
  ></cf-widget>
</template>

<style lang="scss">
.my-pay-widget {
  table {
    margin: 0;
    border: 0;
    width: 100%;
  }
}
</style>
